// Menu wrapper
.menu-list {
	width: 224px;
	height: 142px;
	.menu-item {
		margin-left: 0;
	}
}

.popper {
	padding-bottom: 8px;
}

.menu-body {
	overflow-y: auto;
}

// Menu item
.menu-item {
	width: 100%;
	justify-content: flex-start;
	border-radius: 0;
	padding: 11px 16px;
	text-align: left;
	font-weight: 500;
	height: 42px;

	&.separate {
		border-top: 1px solid rgba(22, 24, 35, 0.12);
	}

	&:hover {
		background-color: rgba(22, 24, 35, 0.03);
	}
}

//Header
.header {
	position: relative;
	flex-shrink: 0;
	height: 50px;
	margin-top: -8px;
}

.back-btn {
	width: 50px;
	height: 100%;
	background-color: transparent;
	cursor: pointer;
}

.header-title {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
